.MessageWrapper {
    display: block;

    background: transparent;
    opacity: 0.9;
}
.MessageWrapper:not(:last-child) {
    margin-bottom: 2px;
}

.MessageWrapper .wrapper-title {
    color: white;
    padding: 2px 0.5rem;
    font-size: 1.5rem;
    background: #444444;
    opacity: 0.9;
}

.MessageWrapper .error-title {
    color: #ff8888; /* #ff5d5d */
}
.MessageWrapper .warning-title {
    color: #f09b4d;
}

.MessageWrapper .wrapper-title.warning {
    display: flex;
}

.MessageWrapper .show-all-text {
    color: #4fb3bf;
    margin-left: auto;
    margin-right: 0.5rem;
    border: none;
    user-select: none;
}

.MessageWrapper .show-all-text:hover {
    cursor: pointer;
}

.MessageWrapper .warning-message-list {
    display: block;
    background: #555555;
    list-style: none;
    margin: 0;
    margin-top: 2px;
    padding: 0 0.5rem;
    font-size: 1rem;
    border-radius: 3px;
    color: #fff;
    opacity: 0.9;
}

.MessageWrapper .warning-message-list > li {
    background: transparent;
}
.MessageWrapper .warning-message-list > li .parameter {
    font-weight: bold;
}
.MessageWrapper .warning-message-list.collapsed {
    height: 0;
    overflow: hidden;
    margin: 0;
    padding: 0;
}
