.radial-extent-2d-xy-plot-chart {
    width: 100%;
    height: 100%;
}

.aim-chart .chart-legend {
    height: 40px;
    padding-left: 60px;
}

.aim-chart .line-chart {
    height: 100%;
}

.aim-chart .chart-legend-item {
    display: inline-flex;
    align-items: center;
}

.aim-chart .chart-legend-group {
    display: inline-block;
    align-items: center;
    width: calc(50% - 30px);
}

.aim-chart .chart-legend-icon {
    width: 40px;
    height: 40px;
}

.aim-chart .svg-circle {
    cx: 50%;
    cy: 50%;
    r: 5;
    stroke: #000000;
}

.aim-chart .svg-circle.empty {
    stroke-width: 1;
    fill: #ffffff;
}

.aim-chart .cross-point {
    stroke: #ff0000;
    stroke-width: 1;
    transform: translate(50%, 50%);
}

.aim-chart .svg-container text {
    font-size: 1rem;
}

.aim-chart .svg-container g.legend text {
    font-size: 0.6rem;
}

.aim-chart .svg-container text.axis {
    font-size: 1.25rem;
}

.aim-chart.radial-extent-2d-xy-plot {
    height: 100%;
}

.radial-extent-2d-xy-plot-chart .controls {
    height: 2rem;
    background-color: grey;
}

.radial-extent-2d-xy-plot-chart .controls .search-select {
    width: 30rem;
    margin-right: 1rem;
    margin-left: 1rem;
}

.radial-extent-2d-xy-plot-chart .controls .search-select i {
    font-size: 1.2rem;
    position: relative;
    top: 0.2rem;
}
