.zoom-buttons {
    display: inline-block;
    position: absolute;
    bottom: 0.25rem;
    right: 1rem;
    z-index: 50;
    border-radius: 0.3125rem;
}

.zoom-buttons div {
    height: 2.1875rem;
    width: 2.1875rem;
    line-height: 2.1875rem;
    text-align: center;
    background-color: #4fb3bf;
    cursor: pointer;
    border-radius: 0.3125rem;
    color: #f5f5f5;
    margin-top: 0.25rem;
    padding: 0.15rem;
}

.zoom-buttons div i {
    font-size: 2rem;
}

.zoom-buttons div:nth-child(1) {
    border-right: 1px solid #dadada;
}

.zoom-buttons .unselect {
    user-select: none;
}
