.ae-details {
    background-color: #4fb3bf;
    color: #f5f5f5;
    cursor: pointer;
    font-size: 1.25rem;
    padding: 0.5rem 0.75rem;
    border-radius: 0.3125rem;
    box-shadow: 0rem 0.125rem 0.25rem rgba(0, 0, 0, 0.14),
        0rem 0.1875rem 0.25rem rgba(0, 0, 0, 0.12), 0rem 1rem 5rem rgba(0, 0, 0, 0.2);
    pointer-events: all;
}

.ae-details.expanded div {
    display: inline-block;
}

.ae-details.expanded {
    font-size: 1rem;
    background-color: #555555;
    width: 34rem;
    cursor: default;
    padding: unset;
    position: relative;
    height: 21rem;
}

.ae-details i {
    font-size: 2rem;
    line-height: 1rem;
}

.ae-details .ae-details-navigation-button.right {
    right: 0.5rem;
}

.ae-details .ae-details-navigation-button.left {
    left: 0.5rem;
}

.ae-details .ae-details-navigation-button i {
    position: relative;
    top: 0.65rem;
}
.ae-details .ae-details-navigation-button {
    position: absolute;
    bottom: 0.5rem;
    line-height: 1rem;
    cursor: pointer;
    user-select: none;
}

.ae-details .ae-details-close {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    cursor: pointer;
}
.ae-details div.feature-values {
    display: block;
    margin-left: 0.5rem;
    margin-top: 0.1rem;
}

.ae-details .feature-values-container {
    height: 20rem;
    width: 100%;
    margin-bottom: 3rem;
    overflow-y: auto;
}
