.content-area {
    width: calc(100% - 33rem);
    height: calc(100% - 4rem);
    position: absolute;
    left: 33rem;
    top: 4rem;
    overflow: hidden;

    display: grid;

    grid-template-rows: auto auto 1fr 1fr;
}

.content-area.chart-panel-maximized {
    grid-template-rows: auto auto 0 1fr;
}
.content-area .map-container {
    grid-row: 3;
    overflow: hidden;
    display: grid;
    grid-template-rows: auto 1fr;
}

.content-area .map-view-container {
    grid-row: 2;
    height: 100%;
    overflow: hidden;
    position: relative;
}

.content-area .azimuth-toggle-container {
    background: #4fb3bf;
    margin-left: 1rem;
    display: flex;
    align-items: center;
    padding: 0 0.5rem;
    box-sizing: border-box;
    height: 2.5rem;
    border-radius: 0.25rem;
    pointer-events: all;
    box-shadow: 0rem 0.125rem 0.25rem rgba(0, 0, 0, 0.14),
        0rem 0.1875rem 0.25rem rgba(0, 0, 0, 0.12), 0rem 1rem 5rem rgba(0, 0, 0, 0.2);
}

.content-area .azimuth-toggle-container > input {
    height: 1rem;
    width: 1rem;
    vertical-align: middle;
}

.content-area .azimuth-toggle-container > label {
    margin-left: 0.5rem;
    font-size: 1rem;
    font-family: "Roboto", sans-serif;
    color: #f5f5f5;
    user-select: none;
    cursor: pointer;
}

.content-area .azimuth-toggle-container > input:disabled ~ label {
    color: #999;
}

.content-area .azimuth-toggle-container.disabled {
    background: #ddd;
    box-shadow: none;
}
