.pane-selector .pane-selector-title {
    font-size: 1.5rem;
    margin-top: 0.5rem;
}

.pane-selector .pane-button {
    display: inline-block;
    background-color: #00838f;
    border: 1px solid #4fb3bf;
    padding: 0.5rem 3.5rem 0.5rem 3.5rem;
    margin-top: 0.5rem;
    cursor: pointer;
}

.pane-selector .pane-button:nth-of-type(2) {
    border-radius: 0.1875rem 0 0 0.1875rem;
}
.pane-selector .pane-button:nth-of-type(3) {
    border-radius: 0 0.1875rem 0.1875rem 0;
}

.pane-selector .pane-button.selected {
    background-color: #4fb3bf;
}

.pane-selector .pane-selector-container {
    margin-left: 1rem;
}
