.calculation-table {
    z-index: 5;
    height: 100%;
    width: 100%;
    text-align: left;
}

.calculation-table table {
    border: 1px solid black;
    width: 95%;
    height: 95%;
}

.calculation-table thead th {
    border-bottom: 1px solid black;
    height: 1.75rem;
    /* padding-left: 1rem; */
    text-align: center;
}

.calculation-table tbody th {
    border-right: 1px solid black;
    padding-left: 1rem;
    text-align: center;
}

.calculation-table tbody tr th:first-of-type {
    width: 30%;
    text-align: left;
}

.calculation-table th:last-of-type {
    border-right: none;
}

.calculation-table select {
    margin-left: 1rem;
    padding-left: 0.5rem;
}
