.Cat-Chart {
    position: relative;
    overflow: hidden;
}

.Cat-Chart .svg-container {
    position: absolute;
    width: 100%;
    height: 100%;
}

.Cat-Chart .canvas-container {
    position: absolute;
}

.Cat-Chart .legend {
    position: absolute;
    /*temp*/
    background: #ffffff;
    right: 15px;
}

.Cat-Chart .popup {
    position: absolute;
    background: #fefefe;
    border: 2pt solid #444444;
    font-weight: 700;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    -webkit-box-shadow: 0 0 10px #000;
    -moz-box-shadow: 0 0 10px #000;
    box-shadow: 0 0 10px #000;
    padding: 1rem;
    display: none;
    pointer-events: none;
    z-index: 50;
}
