.base-map-toggle {
    margin: 0;
    position: absolute;
    right: 1rem;
    bottom: 9.5rem;
}

.base-map-toggle div {
    cursor: pointer;
    text-align: center;
    padding: 0.15rem;
    margin: 0;
    width: 2.1875rem;
    background-color: #4fb3bf;
    color: white;
    opacity: 0.5;
    margin-top: 0.5rem;
    border-radius: 0.3125rem;
}

.base-map-toggle div i {
    font-size: 1.75rem;
    vertical-align: middle;
}

.base-map-toggle .selected {
    opacity: 1;
}
