.info-panel {
    position: absolute;
    top: 0;
    right: 0;
    width: 28rem;
    background-color: #005662;
    height: 100%;
    color: white;
    z-index: 3;
}

.info-panel.open {
    transition: all 500ms;
    transform: translateX(0);
}

.info-panel.closed {
    transition: all 500ms;
    transform: translateX(28rem);
}

.info-panel .controls {
    width: 100%;
    height: 1.5rem;
    text-align: right;
    margin-top: 0.5rem;
    cursor: pointer;
}

.info-panel .controls i {
    vertical-align: middle;
}

.info-panel .content {
    margin-left: 1rem;
    margin-top: 1rem;
    height: calc(100% - 3.5rem);
    overflow-y: auto;
    font-weight: 600;
}

.info-panel .content ul {
    margin-left: -2rem;
    margin-top: 0.5rem;
    font-weight: 200;
}

.info-panel .content li {
    margin-top: 0.25rem;
}

.info-panel .content .references-list {
    margin-bottom: 0.5rem;
}

.show-button {
    position: absolute;
    top: 0;
    right: 0;
    background-color: #005662;
    color: white;
    z-index: 3;
    cursor: pointer;
    border-radius: 0.25rem;
    padding: 0 0.5rem;
    height: 2.5rem;
    line-height: 2.5rem;
    width: 12rem;
    transition: all 500ms;
}

.show-button i {
    vertical-align: middle;
}

.show-button.open {
    transform: translateX(0);
}

.show-button.closed {
    transform: translateX(9.5rem);
}
