.tooltip-wrapper {
    position: absolute;
    border-radius: 4px;
    display: inline-block;
    left: 50%;
    transform: translateX(-50%);
    padding: 6px;
    color: white;
    background: black;
    font-size: 14px;
    font-family: "Roboto", sans-serif;
    line-height: 1;
    z-index: 100;
    white-space: nowrap;
    /*top: calc(-1.5*100% - 2px);*/
    bottom: calc(2rem + 6px);
}

.tooltip-wrapper::before {
    content: " ";
    left: 50%;
    border: solid transparent;
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    border-width: 6px;
    /* margin-left: calc(6px * -1); */
    top: 100%;
    border-top-color: black;
}
