.export-dialog {
    position: absolute;
    top: calc(30% - 10rem);
    left: calc(50% - 16.5rem);
    z-index: 5000;
    width: 33rem;
    height: 21rem;
    background-color: #ffffff;
    border-radius: 0.3125rem;
    padding: 1rem;
    border: rgba(0, 0, 0, 0.25) solid 0.01rem;
}

.export-dialog .close-button {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    cursor: pointer;
    font-size: 1.5rem;
}

.export-dialog .title {
    font-size: 1.6rem;
    text-align: left;
    padding-bottom: 0.5rem;
}

.export-dialog .bold {
    font-weight: bold;
}

.export-dialog .grey {
    color: grey;
    padding: 0.25rem 0 0.5rem 0.5rem;
}

.export-dialog .entry-row i {
    position: relative;
    font-size: 1.5rem;
    width: 1.5rem;
    top: 0.35rem;
    cursor: pointer;
    display: inline-block;
}

.export-dialog .entry-row div.the-grey {
    color: grey;
    font-weight: normal;
}

.export-dialog .entry-row div {
    position: relative;
    margin: 0.8rem 0.25rem;
    display: inline-block;
    font-size: 1rem;
}

.export-dialog .btn.cancel {
    right: 15rem;
    width: 5rem;
    background-color: #858585;
}
.export-dialog .btn.export {
    right: 1.5rem;
    width: 12rem;
    background-color: #4fb3bf;
}

.export-dialog .btn {
    text-align: center;
    position: absolute;
    bottom: 1rem;
    border-radius: 0.125rem;
    color: whitesmoke;
    padding: 0.5rem 0rem;
    cursor: pointer;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12),
        0px 1px 5px rgba(0, 0, 0, 0.2);
}

.export-dialog .grey-background {
    background-color: #858585;
}

.export-dialog .blue-background {
    background-color: #4fb3bf;
}
