.chart-view {
    background: #fff;
    left: 0;
    z-index: 3;
    grid-row: 4;
    position: relative;
    padding: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    overflow: hidden;
}

.chart-view .maximize-window {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    background: transparent;
    color: black;
    border: none;
    cursor: pointer;
    width: 1.5rem;
    height: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.25rem;
    transition: background 0.2s ease 0s;
}

.chart-view .maximize-window > i {
    height: 1.5rem;
    width: 1.5rem;
    font-size: 1.5rem;
}
.chart-view .maximize-window:hover {
    background: rgba(0, 0, 0, 0.06);
}
.chart-view .left-chart {
    grid-column: 1;

    margin-right: 1rem;
    display: grid;

    grid-template-rows: auto 1fr;
}

.chart-view .right-chart {
    grid-column: 2;

    display: grid;

    grid-template-rows: auto 1fr;
}

.chart-view .chart-dropdown-wrapper {
    display: flex;
    flex-direction: row;
    margin-bottom: 1rem;
}

.chart-view .select-container:first-child {
    width: 60%;
    margin-right: 1rem;
}

.chart-view .select-container {
    width: 30%;
}

.chart-view .chart-dropdown {
    width: 100%;
    padding: 0.25rem 0.5rem;
    font-family: "Roboto", sans-serif;
    font-size: 1.15rem;
    color: #222;
}

.chart-view .dropdown-label {
    font-size: 1rem;
    font-weight: bold;
    display: inline-block;
    margin-bottom: 0.25rem;
    color: #222;
}
.chart-view .chart {
    overflow: hidden;
}
