.compass-controls {
    background: unset;
    display: inline-block;
    position: absolute;
    bottom: 0rem;
    right: 3.5rem;
    z-index: 50;
    border-radius: 0.3125rem;
}

.compass-controls div {
    display: inline-block;
    height: 1rem;
    width: 3.375rem;
    line-height: 1rem;
    text-align: center;
    background: linear-gradient(148.14deg, #00fff0 14.44%, #0047ff 87.98%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;
}

.compass-controls div i {
    font-size: 1rem;
}

.compass-controls .unselect {
    user-select: none;
}

.compass-controls img {
    height: 1.5rem;
    padding-top: 0.25rem;
}
