html {
    font-size: 16px;
}

body {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    font-family: "Titillium Web";
    font-style: normal;
    font-weight: normal;
    margin: unset;
    padding: unset;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
    width: 100%;
    height: 100%;
    position: relative;
}
