.unit-controls {
    position: absolute;
    bottom: 0.25rem;
    right: 6.5rem;
    color: #ffffff;
    z-index: 10;
}

.unit-controls .button.selected {
    background-color: #4fb3bf;
}

.unit-controls div {
    display: inline-block;
}

.unit-controls .button {
    margin-right: 0.125rem;
    width: 1.5rem;
    height: 1.125rem;
    border-radius: 0.0625rem;
    box-shadow: 0rem 0.125rem 0.25rem rgba(0, 0, 0, 0.14),
        0rem 0.1875rem 0.25rem rgba(0, 0, 0, 0.12), 0rem 1rem 5rem rgba(0, 0, 0, 0.2);
    text-align: center;
    background-color: rgba(79, 179, 191, 0.5);
    cursor: pointer;
}
