.location-specification .location-specification-row {
    margin-left: 1.5rem;
    width: 28.5rem;
}

.location-specification .location-specification-row p {
    display: inline-block;
    margin: 0;
    margin-top: 0.75rem;
    margin-right: 1rem;
    width: 8.5rem;
    text-align: right;
}

.location-specification .location-specification-row input,
.location-specification .location-specification-row select {
    width: 11.15rem;
    height: 2rem;
    margin-top: 0.5rem;
    padding: 0;
    padding-left: 0.5rem;
    font-family: "Roboto";
    font-size: 1.25rem;
}

.location-specification .location-specification-row select {
    width: 12rem;
    padding-left: 0.25rem;
}

.location-specification .location-specification-row input:focus-within,
.location-specification .location-specification-row select:focus-within {
    z-index: 1;
    border-color: #000;
}

.location-specification .location-specification-row .second-input,
.location-specification .location-specification-row p:nth-child(2) {
    padding-left: 0.25rem;
    width: 6rem;
    text-align: left;
    margin: 0;
    margin-left: 0.5rem;
}

.location-specification-row p.units {
    width: 4rem;
    margin: 0;
    margin-left: 0.85rem;
    text-align: left;
}

.location-specification-row p.transform-error {
    font-weight: bold;
    color: #ffff00;
    font-size: 1rem;
    width: 26rem;
    text-align: center;
    margin-right: 0;
}
