.input-parameters-section {
    width: 100%;
    box-sizing: border-box;
    padding: 0 1rem 1rem 1rem;
}

.input-parameters-section .section-header {
    font-weight: normal;
    font-family: "Roboto", sans-serif;
    font-size: 1.5rem;
    margin: 1rem 0 0.25rem 0;
}

.input-parameters-section .input-parameter-container {
    display: grid;
    grid-template-columns: auto 1fr 6rem;
    row-gap: 0.5rem;
    column-gap: 0.5rem;
    grid-auto-rows: auto;
    align-items: center;
}

.input-parameters-section .input-parameter-prelabel {
    grid-column: 1;
    text-align: right;
    margin-right: 0.5rem;
    font-size: 1rem;
}

.input-parameters-section .input-parameter-select {
    grid-column: 3;
    width: 100%;
    box-sizing: border-box;
    height: calc(2rem + 4px);
    padding-left: 0.25rem;
    border: 2px solid #858585;
    font-family: "Roboto", sans-serif;
    font-size: 1.25rem;
    margin-left: 0.4rem;
}

.input-parameters-section .input-parameter-select:disabled {
    color: #999;
    background: #ddd;
    cursor: not-allowed;
    opacity: 1;
}

.input-parameters-section .input-parameter-wrapper {
    grid-column: 2;
    width: 100%;
    position: relative;
    display: flex;
    background: #fff;
    border: 2px solid #858585;
    border-radius: 3px;
    align-items: center;
}

.input-parameters-section .input-parameter-wrapper:focus-within {
    z-index: 1;
    border-color: #000;
    /*border-style: dashed;*/
}

.input-parameters-section .input-parameter-input {
    border: none;
    box-sizing: border-box;
    width: 100%;
    height: 2rem;
    padding: 0 0 0 0.5rem;
    font-family: "Roboto", sans-serif;
    font-size: 1.25rem;
}

.input-parameters-section .input-parameter-input:focus {
    outline: 0;
}

.input-parameters-section .input-parameter-wrapper.has-error {
    border: 3px solid #ff0000;
}

.input-parameters-section .input-parameter-wrapper.has-warning {
    border: 3px solid orange;
}

.input-parameters-section .input-parameter-input:disabled {
    background: #ddd;
    color: #999;
    cursor: not-allowed;
}

.input-parameters-section .input-parameter-postlabel {
    grid-column: 3;
    padding-left: 1rem;
    text-align: left;
}

.input-parameters-section .select-parameter {
    grid-column: 2;
    border: 2px solid #858585;
    box-sizing: border-box;
    width: calc(100% + 4px);
    height: 2.5rem;
    padding-left: 0.25rem;
    border-radius: 3px;
    font-family: "Roboto", sans-serif;
    font-size: 1.25rem;
}

.input-parameters-section .select-parameter option {
    font-size: 1.15rem;
}

.permissible-range-text,
.warning-range-text {
    margin: 0;
    font-size: 1rem;
}
.permissible-range-text .highlight {
    color: #b3ffff;
}

.highlight-additional {
    margin-left: 10rem;
    padding-top: 0.25rem;
}

.warning-highlight {
    color: #f6e05e;
}

.warning-highlight-additional {
    margin-left: 8rem;
    padding-top: 0.25rem;
}

.warning-range-text {
    padding-top: 8px;
}

.warning-range-text-additional {
    font-style: italic;
}

.reset-icon {
    margin-right: 0.25rem;
    font-size: 1.75rem !important;
    color: #00838f;
    background: none !important;
    cursor: pointer;
}
