.export-chart-container {
    position: absolute;
    right: 5000px;
    height: 500px;
    width: 2500px;
}

.export-chart-container .chart-view {
    background: #fff;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 3;
    grid-row: unset;
    padding: unset;
    display: block;
    grid-template-columns: unset;
    grid-template-rows: unset;
    overflow: hidden;
}

.export-chart-container .chart-view .left-chart,
.export-chart-container .chart-view .right-chart {
    display: inline-block;
    width: 50%;
    height: 100%;
}

.export-chart-container .chart-view .left-chart .chart,
.export-chart-container .chart-view .right-chart .chart {
    height: 100%;
}
