.single-select {
    margin-left: 1rem;
    padding: 0.1rem 0.5rem 0.2rem 0.25rem;
    height: 2rem;
    color: #ffffff;
    background: #00838f;
    box-shadow: 0rem 0.125rem 0.25rem rgba(0, 0, 0, 0.14),
        0rem 0.1875rem 0.25rem rgba(0, 0, 0, 0.12), 0rem 1rem 5rem rgba(0, 0, 0, 0.2);
    border-radius: 0.1875rem;
    cursor: pointer;
}

.single-select.active {
    background-color: #4fb3bf;
}

.single-select p {
    display: inline-block;
    font-size: 1.25rem;
    margin: 0;
    vertical-align: middle;
}

.single-select i {
    margin: 0;
    vertical-align: middle;
    line-height: 2.1875rem;
}
