.analysis-configuration {
    height: 100%;
    background-color: #00838f;
    box-sizing: border-box;
    color: white;
    display: grid;
    grid-template-rows: 1fr auto;
    user-select: none;
    overflow: hidden;
}

.analysis-configuration .analysis-configuration-container {
    grid-row: 1;
    height: 100%;
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
}

.calculate-btn-container {
    grid-row: 2;
    padding: 1rem;
    background: #005662;
}

.calculate-btn {
    padding: 0.5rem 0;
    width: 100%;
    background: #b3ffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12),
        0px 1px 5px rgba(0, 0, 0, 0.2);
    border-radius: 0.25rem;
    border: none;
    cursor: pointer;
    font-size: 1.25rem;
    transition: background 0.25s ease 0s;
    font-family: "Roboto", sans-serif;
}

.calculate-btn:hover {
    background: #d4ffff;
}

.calculate-btn:active {
    background: #ffffff;
    box-shadow: none;
}

.calculate-btn:disabled {
    color: #999;
    background: #ddd;
    cursor: not-allowed;
}

.analysis-configuration .title {
    font-size: 2rem;
    position: relative;
    font-weight: 400;
    margin: 0;
    padding: 0.5625rem 1rem;
    box-shadow: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.2), 0 0.125rem 0.125rem rgba(0, 0, 0, 0.12),
        0 0 0.125rem rgba(0, 0, 0, 0.14);
}
