.applied-algorithms .algorithm-row {
    cursor: pointer;
    padding-left: 2rem;
    display: flex;
    align-items: center;
}

.applied-algorithms .algorithm-row i,
.applied-algorithms .algorithm-row p {
    font-size: 1.5rem;
    margin: 0;
    margin-right: 1rem;
    margin-top: 0.25rem;
}

.applied-algorithms .algorithm-row p {
    font-size: 1rem;
    margin-right: auto;
}

.applied-algorithms .color-box {
    width: 2.5rem;
    height: 1rem;
    border: 1px solid white;
}

.not-implemented-text {
    margin-left: 2rem;
}
