.import-dialog {
    width: 33rem;
    height: 20rem;
    background-color: #ffffff;
    position: absolute;
    top: 7.5rem;
    left: calc(50% - 15.5rem);
    z-index: 6;
    border-radius: 0.3125rem;
    padding-left: 1rem;
    user-select: none;
}

.import-dialog .close-button {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    cursor: pointer;
    font-size: 1.5rem;
}

.import-dialog .title {
    margin-top: 1.5rem;
    font-size: 1.6rem;
    margin-bottom: 0.5rem;
}

.import-dialog .bold {
    font-weight: bold;
}

.import-dialog .grey {
    color: grey;
    padding: 0.25rem 0 0.5rem 0.5rem;
}

.import-dialog .select-button {
    width: 8rem;
    height: 2rem;
    background-color: #4fb3bf;
    color: white;
    text-align: center;
    line-height: 2rem;
    border-radius: 0.1875rem;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12),
        0px 1px 5px rgba(0, 0, 0, 0.2);
    margin-top: 1rem;
    margin-bottom: 1rem;
    cursor: pointer;
    /* visibility: hidden; */
}

.import-dialog input[type="file"] {
    position: absolute;
    top: 7rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 8rem;
    cursor: pointer;
    opacity: 0%;
}

.import-dialog p {
    display: inline;
    font-weight: 400;
    margin-left: 0.5rem;
}

.import-dialog .cancel-button {
    text-align: center;
    position: absolute;
    border-radius: 0.125rem;
    padding: 0.5rem 0rem;
    cursor: pointer;
    color: whitesmoke;
    background: #858585;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12),
        0px 1px 5px rgba(0, 0, 0, 0.2);
    bottom: 1.5rem;
    right: 16rem;
    width: 6rem;
    /*height: 35px;
    line-height: 35px;*/
}

.import-dialog .load-button {
    text-align: center;
    position: absolute;
    border-radius: 0.125rem;
    padding: 0.5rem 0rem;
    cursor: pointer;
    appearance: none;
    border: none;
    color: whitesmoke;
    background: #4fb3bf;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.14), 0px 3px 4px rgba(0, 0, 0, 0.12),
        0px 1px 5px rgba(0, 0, 0, 0.2);
    bottom: 1.5rem;
    right: 1.5rem;
    width: 12rem;
    /*height: 35px;
    line-height: 35px;*/
    font-size: 1rem;
}

.import-dialog .load-button:disabled {
    color: #999;
    background: #ddd;
}
