.react-open-layers-preparation {
    width: 100%;
    height: 100%;
    background-color: aqua;
}

#root .ol-scale-line {
    float: right;
    bottom: 0.25rem;
    right: 3rem;
    height: 1rem;
    background: rgba(245, 245, 245, 0);
    font-family: Aldrich;
}

#root .react-open-layers-preparation .ol-scale-line-inner {
    position: absolute;
    right: 7.75rem;
    color: #121212;
    font-size: 0.8rem;
    border-radius: 0.1875rem;
    line-height: 1rem;
    border-color: #121212;
}

.ae-details-container {
    z-index: 1000;
    left: 0.5rem;
    bottom: 0.5rem;
    position: absolute;
    display: flex;
    align-items: flex-end;
    pointer-events: none;
}

.ol-overviewmap:not(.ol-collapsed) button {
    left: 4px;
    top: 4px;
}

#root .minimap-container {
    left: 0.5rem;
    top: 0.5rem;
    position: absolute;
    display: flex;
    align-items: flex-start;
}

#root .ol-overviewmap {
    background-color: #4fb3bf;
    position: unset;
}
