.login-screen-container {
    position: absolute;
    top: 0;
    left: 0;
    min-height: 100vh;
    max-height: auto;
    width: 100%;
    background-color: lightgrey;
    z-index: 100;
    background-image: url("../../resources/imgs/background.jpg");
    background-size: 100%;
    background-repeat: no-repeat;
    overflow-y: auto;
}

.center-backdrop {
    position: absolute;
    top: 0;
    min-height: 100vh;

    width: 60%;
    margin-left: 20%;
    background-color: white;
}

.login-screen-box {
    width: 30rem;
    background-color: rgb(139, 159, 224);
    padding: 1.5rem;
    border-radius: 2rem;
    display: block;
    margin: 0.5rem auto 0 auto;
    padding-bottom: 1rem;
}

.login-screen-box .title {
    text-align: center;
    margin-top: -1rem;
    font-weight: 200;
}

.login-screen-box .subtitle {
    text-align: center;
}

.login-screen-box .login-label {
    text-align: right;
    margin-left: 8rem;
}

.login-screen-box input {
    margin-left: 1rem;
    width: 12rem;
    height: 1.5rem;
    line-height: 1.5rem;
    font-size: 1.25rem;
    padding-left: 0.25rem;
    /*float: right;*/
}

.login-screen-box h2 {
    margin: 0.75rem auto;
}

.login-screen-box p {
    font-size: 1.25rem;
    margin: 0.75rem auto;
}

.login-screen-box .button {
    background-color: lightblue;
    width: 5rem;
    border-radius: 0.25rem;
    text-align: center;
    border: none;
    display: block;
    margin: 1rem auto 0 auto;
    font-family: serif;
    cursor: pointer;
    font-size: 1.25rem;
}

.login-screen-box p.error {
    bottom: 2.5rem;
    color: rgb(218, 15, 15);
}

section.disclaimer-container {
    text-align: justify;
    font-size: 1.25rem;
    padding: 1rem 4rem 2rem;
}

section.disclaimer-container h4 {
    text-align: center;
    padding: 0;
    margin: 1.5rem 0 -0.75rem 0;
}

#pnnl {
    text-align: center;
    margin-top: 2rem;
}
