.scenario-title {
    width: 100%;
    height: 3.5rem;
    background-color: #4fb3bf;
    color: white;
    display: grid;
    align-items: center;
    grid-row: 1;
    grid-template-columns: auto 1fr auto;
}

.scenario-title .error {
    color: red;
}

.scenario-title .title {
    line-height: 3.5rem;
    margin: 0 1rem;
    grid-column: 1;
}

.scenario-title input {
    height: 2rem;

    grid-column: 2;
    line-height: 2rem;
    padding-left: 1rem;
    border-radius: 0.1875rem;
}

.scenario-title .char-counter {
    margin-left: 1rem;
    margin-right: 1rem;
    grid-column: 3;
}
