.reset-view {
    position: absolute;
    display: flex;
    flex-direction: column;
    bottom: 5.75rem;
    right: 1rem;
    z-index: 50;
    cursor: pointer;
}

.reset-view i {
    font-size: 1.75rem;
    user-select: none;
    vertical-align: middle;
}

.reset-view .reset-view-button {
    line-height: 2.1875rem;
    height: 2.1875rem;
    width: 2.1875rem;
    padding: 0.15rem;
    text-align: center;
    border-radius: 0.3125rem;
    background-color: #4fb3bf;
    color: #f5f5f5;
}
