.navigation-bar {
    grid-column: 1 / span 2;
    height: 100%;
    background-color: #005662;
    color: white;
    padding-left: 1rem;
}

.navigation-bar .title {
    font-size: 3rem;
    margin: 0;
    font-weight: 400;
    display: inline-block;
}

.navigation-bar .version {
    display: inline-block;
    margin-left: 1rem;
}

.navigation-bar .buttons {
    position: absolute;
    right: 0.5rem;
    top: 1rem;
    cursor: pointer;
}

.navigation-bar .button,
.navigation-bar button {
    height: 2.1875rem;
    border: none;
    background-color: #4fb3bf;
    color: #fff;
    box-shadow: 0 0.0625rem 0.3125rem rgba(0, 0, 0, 0.2), 0px 0.1875rem 0.25rem rgba(0, 0, 0, 0.12),
        0px 0.125rem 0.25rem rgba(0, 0, 0, 0.14);
    border-radius: 0.1875rem;
    display: inline-block;
    padding: 0 1rem 0 1rem;
    margin: 0 0.5rem 0 0.5rem;
    line-height: 2.1875rem;
    text-decoration: none;
    font-size: 1rem;
}

.navigation-bar a.button:link,
.navigation-bar button:link {
    color: #fff;
}

.navigation-bar a.button:visited,
.navigation-bar button:visited {
    color: #fff;
}

.navigation-bar button:disabled {
    color: #999;
    background: #ddd;
    cursor: not-allowed;
}

.material-icons-close {
    color: #000;
}

.navigation-bar .disclaimer-overlay {
    display: flex;
    width: 100vw;
    height: 100vh;
    z-index: 9999;
    justify-content: center;
    align-items: center;
    position: fixed;
    left: 0;
    background: rgba(0, 0, 0, 0.48);
}

.navigation-bar .disclaimer-popup {
    padding: 16px;
    background: #fff;
    font-family: serif;
    text-align: center;
    position: relative;
    color: #161d2d;
    font-size: 1.3rem;
    margin: 0 4rem;
    border-radius: 4px;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.navigation-bar .disclaimer-popup .close-btn {
    position: absolute;
    top: 4px;
    right: 8px;
    height: 32px;
    width: 32px;
    display: flex;
    align-items: center;
    background: none;
    appearance: none;
    border: none;
    outline: 0;
    justify-content: center;
    border-radius: 0.25rem;
    cursor: pointer;
    transition: 250ms all ease;
}

.navigation-bar .disclaimer-popup .close-btn > i {
    font-size: 24px;
    height: 24px;
    width: 24px;
}

.navigation-bar .disclaimer-popup .close-btn:hover {
    background: rgba(0, 0, 0, 0.08);
}

.navigation-bar .disclaimer-popup .close-disclaimer-btn {
    background: #4fb3bf;
    color: #fff;
    border: none;
    appearance: none;
    border-radius: 0.1875rem;
    padding: 0 1rem 0 1rem;
    line-height: 2.1875rem;
    cursor: pointer;
}

.navigation-bar .disclaimer-popup p {
    margin-left: 2rem;
    margin-right: 2rem;
    text-align: justify;
}

.navigation-bar .disclaimer-popup h4 {
    padding: 0;
    margin: 1.5rem 0 -0.75rem 0;
}

.linkpnnl {
    text-decoration: none;
}
.linkpnnl:link,
.linkpnnl:visited {
    color: #fff;
}
